import React from 'react';
import SearchBar from './search-bar.jsx';
import ProfessionBookList from './profession-book-list';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      message: null,
      isLoading: true,
      view: {
        name: 'searchByProfession',
        params: {}
      }
    };
    this.setView = this.setView.bind(this);
  }

  setView(name, params) {
    this.setState({
      view: {
        name: name,
        params
      }
    });
  }

  componentDidMount() {
    fetch('/api/health-check')
      .then(res => res.json())
      .then(data => this.setState({ message: data.message || data.error }))
      .catch(err => this.setState({ message: err.message }))
      .finally(() => this.setState({ isLoading: false }));
  }

  render() {
    const viewType = this.state.view.name;

    if (viewType === 'searchByProfession') {
      return <SearchBar view={this.setView}/>;
    } else if (viewType === 'professionBookList') {
      return <ProfessionBookList view={this.setView} viewParams={this.state.view.params}/>;
    }

  }
}
